 .container {
          min-height: 100vh;
          background: linear-gradient(135deg, #f0f9ff 0%, #e0e7ff 50%, #f3e8ff 100%);
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 16px;
          font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', Roboto, sans-serif;
        }

        .main-content {
          width: 100%;
          max-width: 400px;
        }

        .header {
          text-align: center;
          margin-bottom: 32px;
        }

        .logo {
          display: inline-flex;
          align-items: center;
          justify-content: center;
          width: 64px;
          height: 64px;
          background: linear-gradient(45deg, #2563eb, #7c3aed);
          border-radius: 50%;
          margin-bottom: 16px;
        }

        .title {
          font-size: 32px;
          font-weight: bold;
          color: #111827;
          margin: 0 0 8px 0;
        }

        .subtitle {
          color: #6b7280;
          margin: 0;
        }

        .form-card {
          background: white;
          border-radius: 16px;
          box-shadow: 0 20px 25px -5px rgba(0, 0, 0, 0.1), 0 10px 10px -5px rgba(0, 0, 0, 0.04);
          border: 1px solid #f3f4f6;
          padding: 32px;
        }

        .form-content {
          display: flex;
          flex-direction: column;
          gap: 24px;
        }

        .form-group {
          display: flex;
          flex-direction: column;
        }

        .label {
          display: block;
          font-size: 14px;
          font-weight: 500;
          color: #374151;
          margin-bottom: 8px;
        }

        .input-wrapper {
          position: relative;
        }

        .input-icon {
          position: absolute;
          left: 12px;
          top: 50%;
          transform: translateY(-50%);
          color: #9ca3af;
          pointer-events: none;
        }

        .input {
          display: block;
          width: 100%;
          padding: 12px 12px 12px 40px;
          border: 1px solid #d1d5db;
          border-radius: 8px;
          font-size: 16px;
          transition: all 0.2s;
          box-sizing: border-box;
        }

        .input:hover {
          border-color: #9ca3af;
        }

        .input:focus {
          outline: none;
          border-color: #3b82f6;
          box-shadow: 0 0 0 3px rgba(59, 130, 246, 0.1);
        }

        .input.error {
          border-color: #ef4444;
          background-color: #fef2f2;
        }

        .password-input {
          padding-right: 48px;
        }

        .password-toggle {
          position: absolute;
          right: 12px;
          top: 50%;
          transform: translateY(-50%);
          background: none;
          border: none;
          cursor: pointer;
          color: #9ca3af;
          transition: color 0.2s;
        }

        .password-toggle:hover {
          color: #6b7280;
        }

        .error-text {
          margin-top: 8px;
          font-size: 14px;
          color: #ef4444;
        }

        .help-text {
          margin-top: 8px;
          font-size: 12px;
          color: #6b7280;
        }

        .submit-btn {
          width: 100%;
          display: flex;
          align-items: center;
          justify-content: center;
          padding: 12px 16px;
          border: none;
          border-radius: 8px;
          font-size: 16px;
          font-weight: 500;
          color: white;
          background: linear-gradient(45deg, #2563eb, #7c3aed);
          cursor: pointer;
          transition: all 0.2s;
        }

        .submit-btn:hover:not(:disabled) {
          background: linear-gradient(45deg, #1d4ed8, #6d28d9);
          transform: translateY(-1px);
        }

        .submit-btn:disabled {
          opacity: 0.5;
          cursor: not-allowed;
        }

        .btn-icon {
          margin-right: 8px;
        }

        .spinner {
          width: 20px;
          height: 20px;
          border: 2px solid white;
          border-top-color: transparent;
          border-radius: 50%;
          animation: spin 1s linear infinite;
          margin-right: 8px;
        }

        @keyframes spin {
          to {
            transform: rotate(360deg);
          }
        }

        .message {
          padding: 16px;
          border-radius: 8px;
          font-size: 14px;
          margin-top: 16px;
        }

        .message.success {
          background-color: #f0fdf4;
          color: #166534;
          border: 1px solid #bbf7d0;
        }

        .message.error {
          background-color: #fef2f2;
          color: #dc2626;
          border: 1px solid #fecaca;
        }

        .switch-mode {
          margin-top: 24px;
          padding-top: 24px;
          border-top: 1px solid #f3f4f6;
          text-align: center;
        }

        .switch-text {
          font-size: 14px;
          color: #6b7280;
        }

        .switch-link {
          margin-left: 8px;
          font-weight: 500;
          color: #2563eb;
          background: none;
          border: none;
          cursor: pointer;
          text-decoration: underline;
          transition: color 0.2s;
        }

        .switch-link:hover {
          color: #1d4ed8;
        }

        .footer {
          text-align: center;
          margin-top: 32px;
          font-size: 14px;
          color: #9ca3af;
        }

        @media (max-width: 480px) {
          .form-card {
            padding: 24px;
          }
          
          .title {
            font-size: 28px;
          }
        }